import axios from 'axios'

// 获取轮播图的api
// type:资源类型,对应以下类型,默认为 0 即 PC
// 0: pc  1: android  2: iphone  3: ipad
let baseUrl = "http://cloud-music.pl-fe.cn"
export function getBanner(type = 0){
    return axios.get(`${baseUrl}/banner?type=${type}`);
}

export function getMlist(limit=8) {
    return axios.get(`${baseUrl}/personalized?limit=${limit}`);
}


export function getPlayList(id) {
    return axios.get(`${baseUrl}/playlist/detail?id=${id}`)
}

export function getLyric(id) {
    return axios.get(`${baseUrl}/lyric?id=${id}`)
}

export function searchMusic(keywords='周杰伦') {
    return axios.get(`${baseUrl}/cloudsearch?keywords=${keywords}`)
}

export function sendma(phone) {
    return axios.get(`${baseUrl}/captcha/sent?phone=${phone}`)
}

export function phoneLogin(phone, password) {
    return axios.get(`${baseUrl}/captcha/verify?phone=${phone}&captcha=${password}`)
}

export function getNewsongs(limit = 24) {
    return axios.get(`${baseUrl}/personalized/newsong?limit=${limit}`)
}

