<template>
    <div class="musicList">
        <div class="musicList-top">
            <div class="title">发现好歌单</div>
            <div class="more">查看更多</div>
        </div>
        <div class="mlist">
            <swiper
                :modules="modules"
                :slides-per-view="3.1"
                :space-between="15"
                @swiper="onSwiper"
                @slideChange="onSlideChange"
            >
                    <swiper-slide v-for="(item, i) in pics" :key="i">
                        <router-link :to="{path:'/listview', query:{id:item.id}}">
                            <img :src="item.picUrl" :alt="item.name">
                            <div class="name">{{item.name}}</div>
                            <div class="count" >
                                <svg class="icon" aria-hidden="true">
                                    <use xlink:href="#icon-w24gl-play"></use>
                                </svg>
                                <span>{{changeVaule(item.playCount)}}</span>
                            </div>
                        </router-link>
                    </swiper-slide>
            </swiper>
        </div>
    </div>
</template>

<script>
import { Swiper, SwiperSlide } from 'swiper/vue';
import {getMlist} from '@/api/index.js'

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
export default {
    data(){
        return{
            pics:[]
        }
    },
    methods: {
        changeVaule(num){
            let res = 0;
            if(num>=100000000){
                res = num / 100000000
                res = res.toFixed(2) + '亿'
                return res
            }else if(num>=10000){
                res = num / 10000
                res = res.toFixed(2) + '万'
                return res
            }
        }
    },
    components: {
      Swiper,
      SwiperSlide,
    },
    setup() {
      const onSwiper = (swiper) => {
        console.log(swiper);
      };
      const onSlideChange = () => {
        console.log();
      };
      return {
        onSwiper,
        onSlideChange,
        // modules: [A11y],
      };
    },
    async mounted () {
        let res = await getMlist();
        this.pics = res.data.result
    }
}
</script>

<style lang='less' scoped>
.musicList {
    width: 7.5rem;
    .musicList-top{
        display: flex;
        height: 0.7rem;
        padding: 0 0.3rem;
        align-items: center;
        justify-content: space-between;
        .title{
            font-size: 0.35rem;
            font-weight: 600;
        }
        .more {
            border: 1px solid #ccc;
            font-size: 0.22rem;
            height: 0.4rem;
            width: 5.6em;
            text-align: center;
            line-height: 0.4rem;
            border-radius: 0.5rem;
        }

    }
    .mlist {
        width: 7.5rem;
        height: 3.2rem;
        .swiper::v-deep{
            z-index: 0;
            width: 7.5rem;
            height: 2.9rem;
            padding: 0.25rem;
        }
        .swiper-slide {
            position: relative;
            display: flex;
            flex-direction: column;
            img {
                display: block;
                width: 100%;
                height: auto;
                border-radius: 0.2rem;
            }
            .name {
                width: 100%;
                height: 0.9rem;
                font-size: 0.2rem;
            }
            .count {
                position: absolute;
                right: 0.1rem;
                top: 0.05rem;
                color: #fff;
                font-size: 0.2rem;
                display: flex;
                align-items: center;
                .icon {
                    width: 0.2rem;
                    height: 0.2rem;
                }
            }
        }
    }
}

</style>