<template>
<div class="box">
    <div class="playController">
        <div class="left" @click="show=!show">
            <img :src="playlist[playCurrentIndex].al.picUrl" alt="">
            <div class="content">
                <div class="title">{{playlist[playCurrentIndex].name}}</div>
                <div class="tips">横滑可以切换上下首哦</div>
            </div>
        </div>
        <div class="right">
            <svg v-if='paused' class="icon" aria-hidden="true" @click="play()">
                <use xlink:href="#icon-w24gl-play"></use>
            </svg>
            <svg v-else class="icon" aria-hidden="true" @click="play()">
                <use xlink:href="#icon-zanting1"></use>
            </svg>
            <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-24gl-playlistHeart"></use>
            </svg>
        </div>

        <play-music :play='play' :paused='paused' @back="show=!show" v-show="show" :playDetail="playlist[playCurrentIndex]"></play-music>
        <audio ref="audio" autoplay :src="`https://music.163.com/song/media/outer/url?id=${playlist[playCurrentIndex].id}.mp3`"></audio>
    </div>
</div>
    
</template>

<script>
import {mapState, mapMutations} from 'vuex'
import playMusic from '@/components/PlayMusic.vue'

export default {
    data () {
        return {
            paused:true,
            show:false
        }
    },
    computed: {
        ...mapState(['playlist', 'playCurrentIndex', 'ok'])
    },
    mounted () {
        this.$store.dispatch('reqLyric', {id: this.playlist[this.playCurrentIndex].id,})
        
    },
    updated () {
        this.$store.dispatch('reqLyric', {id: this.playlist[this.playCurrentIndex].id,})
    },
    methods: {
        play(){

            if(this.$refs.audio.paused){
                this.$refs.audio.play()
                this.paused = false
                this.updateTime()
            }else{
                this.$refs.audio.pause()
                this.paused = true
                clearInterval(this.$store.state.id)
            }
        },
        
        updateTime() {
            this.$store.state.id = setInterval(() => {
                this.$store.commit('setCurrentTime',this.$refs.audio.currentTime)
            }, 10);
        }
    },
   components:{
        playMusic,
   }
}
</script>

<style lang="less" scoped>
.box{
    width: 7.5rem;
    height: 0.9rem;
}
.playController{
    width: 7.5rem;
    height: 1.0rem;
    position: fixed;
    left: 0;
    bottom: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    border-top: 0.03rem solid rgb(219, 219, 219);
    z-index: 100;
    .left{
        display: flex;
        img {
            width: 0.8rem;
            height: 0.8rem;
            border-radius: 0.4rem;
            margin: 0 0.1rem;
        }
        .content {
            padding:0 0.05rem;
            .title{
                width: 4.20rem;
                overflow: hidden;
                display: -webkit-box;
                text-overflow: ellipsis;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
            }
            .tips {
                font-size: 0.2rem;
                color: #666;
            }
        }
    }
    .right{
        .icon{
            width: 0.4rem;
            height: 0.4rem;
            margin: 0 0.2rem;
        }
    }
}
</style>