import { createStore } from 'vuex'
import {getLyric, phoneLogin, sendma} from '@/api/index.js'

export default createStore({
  state() {
    return {
      playlist: [{
        name: '找',
        id: 1861091244,
        al: {
          id: 130338563,
          name: "找",
          pic: 109951166177140080,
          picUrl: "http://p4.music.126.net/Ty3IV9PhbF4XKRuV17o4rQ==/109951166177140078.jpg",
          pic_str: "109951166177140078"
        },
        ar: [{
          name:"汪川"
        }]
      }],
      playCurrentIndex: 0,
      lyric: '',
      currentTime: 0,
      intervalId: 0,
      user: {
        islogin: false,
        username: '未登录',
        
      }
    }
    
  },
  getters: {
    lyricList: function (state) {
      let arr = new Array();
      arr = state.lyric.split(/\n/).map((item, i,arr) => {
        let min = parseInt(item.slice(1, 3))
        let second = parseInt(item.slice(4, 6))
        let mill = parseInt(item.slice(7, 9))

        return {
          min, second, mill,
          lyric: item.split("]")[1],
          content:item,
          time:mill+second*1000+min*60*1000
        }
      })
      return arr
    }
  },
  mutations: {
    setPlaylist: function (state, value) {
      state.playlist = value
    },
    pushPlaylist: function (state, value) {
      state.playlist.push(value)
    },
    setPlayIndex:function(state, value) {
      state.playCurrentIndex = value
    },
    setLyric:function(state, value) {
      state.lyric = value
    },
    setCurrentTime:function(state, value) {
      state.currentTime = value
    },
    setUsername(state, value) {
      
    }
  },
  actions: {
    async reqLyric(content, payload) {
      let res = await getLyric(payload.id)
      content.commit('setLyric',res.data.lrc.lyric)

    },
    async send(content, payload) {
      // console.log(payload);
      let res = await sendma(payload.phone)
      console.log(res);
    },
    async forcertain(content, payload) {
      // console.log(payload);
      let res = await phoneLogin(payload.phone, payload.password)
      console.log(res);
      if (res.data.data) {
        content.state.user.islogin = true
        content.commit("setUsername", content.state.user)
        return true
      } else {
        return false
      }
    }

  },
  modules: {
  }
})
