<template>
    <div class="playMusic">
        <div class="bg" :style="{backgroundImage:`url(${playDetail.al.picUrl})`}"></div>
        <div class="playTop">
            <div class="back" @click="$emit('back')">
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-zuojiantou"></use>
                </svg>
            </div>
            <div class="center">
                <marquee behavior="scroll" direction="left">{{playDetail.name}}</marquee>
                <div class="author">{{playDetail.ar[0].name}}</div>
            </div>
            <div class="share">
                <svg class="icon fenx" aria-hidden="true">
                    <use xlink:href="#icon-fenxiang"></use>
                </svg>
            </div>
        </div>
        <div class="playContent" v-if="isLyric" @click='change'>
            <img class="needle" :class="{active:!paused}" src="@/assets/imgs/needle-ip6.png" alt="">
            <img class="disc" :class="{active:!paused}" src="@/assets/imgs/disc-ip6.png" alt="" @click='change'>
            <img class="playimg" :class="{active:!paused}" :src="playDetail.al.picUrl" alt="">
        </div>
        <div class="playLyric" ref="playLyric" v-else @click='change'>
            <p :class="{active:((currentTime*1000>=$store.getters.lyricList[i].time)&&(currentTime*1000<$store.getters.lyricList[i+1].time))}" v-for="(item,i) in $store.getters.lyricList" :key='i'>{{item.lyric}}</p>
        </div>
        <div class="progress"></div>
        <div class="playFooter">
             <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-caozuo-xunhuan1"></use>
            </svg>
             <svg class="icon" aria-hidden="true" @click="goPlay(-1)">
                <use xlink:href="#icon-shangyishoushangyige"></use>
            </svg>
            <svg v-if="paused" class="icon play" aria-hidden="true" @click="play">
                <use xlink:href="#icon-gl-playCircle"></use>
            </svg>
            <svg v-else class="icon play" aria-hidden="true" @click="play">
                <use xlink:href="#icon-zanting1-copy"></use>
            </svg>
             <svg class="icon" aria-hidden="true" @click="goPlay(1)">
                <use xlink:href="#icon-xiayigexiayishou"></use>
            </svg>
             <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-gl-playlistHeart-copy"></use>
            </svg>
        </div>
    </div>
</template>

<script>
import {mapState} from 'vuex'

export default {
    props: ['playDetail','paused', 'play'],
    data () {
        return {
            isLyric:true,
        }
    },
    computed: {
        ...mapState(['lyric','currentTime', 'playlist','playCurrentIndex']),
        
    },
    watch: {
        currentTime:function(newValue){
            if(!this.isLyric){
                console.log(newValue);
                let p = document.querySelector('p.active')
                console.log([p]);
                let offsetTop = 0
                if(p){
                    offsetTop = p.offsetTop;
                    this.$refs.playLyric.scrollTop = offsetTop-120
                }
                
            }
            
            
        }
    },
    methods: {
        goPlay(type){
            console.log(type);
            console.log(this.playCurrentIndex);
            console.log(this.playlist);
            let count = this.playCurrentIndex+type
            if(count === 0){
                count = this.playlist.length - 1
            }
            else if(count === this.playlist.length){
                count = 0
            }
            this.$store.commit("setPlayIndex",count)
        },
        change(){
            this.isLyric = !this.isLyric
        }
    }
    
}
</script>

<style lang="less" scoped>
@keyframes rotate{
    0%{
        transform: rotate(0);
      }
    100%{
         transform: rotate(360deg);
    	}
}

.playMusic{
    z-index: 1000;
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: #fff;
    .bg{
        position: absolute;
        left: 0;
        top: 0;
        width: 100vw;
        height: 100vh;
        background-size: auto 100%;
        filter: blur(150px);
        background-color: #fff;
        z-index: -1;
    }
    .playTop {
        display: flex;
        justify-content:space-between;
        width: 7.5rem;
        height: 1.5rem;
        position: absolute;
        left: 0;
        top: 0.3rem;
        color: #fff;
        font-size: 0.35rem;
        padding: 0.1rem 0.3rem;
        .icon{
            width: 0.6rem;
            height: 0.6rem;

        }
        .fenx {
            width: 0.43rem;
            height: 0.43rem;
        }
        marquee{
            width: 5.0rem;
        }
        .author{
            font-size: 0.28rem;
            height: 0.5rem;
            color: #666;
            text-align: center;
        }
    }
    .playContent {
        position: absolue;
        width: 7.5rem;
        height: 7.5rem;
        left: 0;
        top: 2.5rem;
        .needle{
            width: 2.5rem;
            height: auto;
            position: absolute;
            left: 45%;
            top: 1.7rem;
            z-index: 1;
            transform-origin: 0.3rem 0;
            transform: rotate(-18deg);
            transition: all 1s;
        }
        .needle.active{
            transition: all 1s;
            transform: rotate(5deg);
        }
        .disc{
            width: 5.5rem;
            height: auto;
            position: absolute;
            left: calc(50% - 2.75rem);
            top: 3.7rem;
            transition: all 10s;
        }
        .disc.active{
            transition: all 400s;
            animation: rotate 1s infinite linear;
        }
        .playimg {
            position: absolute;
            left: calc(50% - 1.7rem);
            top: 4.745rem;
            width: 3.4rem;
            height: 3.4rem;
            border-radius: 1.7rem;
            transition: all 10s;
        }
        .playimg.active{
            transition: all 400s;
            animation: rotate 1s infinite linear;
        }
    }
    .playLyric{
        position: absolute;
        width: 7.5rem;
        height: 8.5rem;
        color: #fff;
        text-align: center;
        left: 0;
        top: calc(50% - 4.5rem);
        overflow: scroll;
        box-shadow:20px 20px 50px rgba(0,0,0,0.01) inset;
        p{
            margin: 0.03rem 0rem;
        }
        p.active{
            color: rgb(255, 47, 47);
            margin: 0.1rem 0;
            font-size: 0.38rem;
            transition: all 0.3s linear;
        }
    }
    .playFooter{
        position: absolute;
        left: 0;
        bottom: 0;
        width: 7.5rem;
        height: 1.5rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 0.6rem;
        margin: 0.15rem 0;
        .icon{
            width: 0.5rem;
            height: 0.5rem;
            fill: #fff;
        }
        .play {
            width: 1rem;
            height: 1rem;
            fill: #fff;
            margin: 0 -0.2rem;
        }
    }
}
</style>