<template>
<div class="swiperc">
  <swiper
      :modules="modules"
      :slides-per-view="1"
      :space-between="0"
      :loop="true"
      :pagination="{ clickable: true }"
      :autoplay='{
              "delay":2500,
              "disableOnInteraction":false
            }'
      @swiper="onSwiper"
      @slideChange="onSlideChange"
    >
      <swiper-slide v-for="(item, i) in imgs" :key="i"><img :src="item.pic" alt=""></swiper-slide>
  </swiper>
</div>
      
</template>

<script>
import {Pagination, A11y ,Autoplay} from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/vue';
import{getBanner} from '@/api/index.js'
// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
export default {
    data(){
        return{
            imgs:[
            require("../assets/imgs/swiper1.jpg"),
            require("../assets/imgs/swiper2.jpg"),
            require("../assets/imgs/swiper3.png")
        ]
        }
    },
    components: {
      Swiper,
      SwiperSlide,
    },
    setup() {
      const onSwiper = (swiper) => {
        console.log(swiper);
      };
      const onSlideChange = () => {
        console.log();
      };
      return {
        onSwiper,
        onSlideChange,
        modules: [Pagination, A11y, Autoplay],
      };
    },
    async mounted(){
      let res = await getBanner(1)
      this.imgs = res.data.banners
    }
}
</script>

<style lang="less" scoped>
.swiperc{
  width: 7.5rem;
  
  .swiper::v-deep {
      z-index: 0;
      width: 7.0rem;
      height: 2.7rem;
      border-radius: 0.2rem;
      margin-top: 0.2rem;
      .swiper-slide img{
          width: 100%;
          height: 100%;
      }
  }
}

</style>