<template>
    <div class="newsongs">
        <div class="newsongsTop">
            <div class="title">华语金曲点唱机</div>
            <div class="more">
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-w24gl-play"></use>
                </svg>
                <span>播放全部</span>
            </div>
        </div>
        <div class="newsongsContent">
            <swiper
                :modules="modules"
                :slides-per-view="1.1"
                :space-between="30"
                @swiper="onSwiper"
                @slideChange="onSlideChange"
            >
                    <swiper-slide v-for="(item, i) in songs" :key="i">
                        <div class="box">
                            <div class="list">
                                <div class="pic"><img :src="item[0].picUrl" :alt="item[0].name"></div>
                                <div class="name">{{item[0].name}}-{{item[0].song.artists[0].name}}</div>
                                <svg class="icon" aria-hidden="true">
                                    <use xlink:href="#icon-gl-playCircle-copy"></use>
                                </svg>
                            </div>
                            <div class="list">
                                <div class="pic"><img :src="item[1].picUrl" :alt="item[1].name"></div>
                                <div class="name">{{item[1].name}}-{{item[1].song.artists[0].name}}</div>
                                <svg class="icon" aria-hidden="true">
                                    <use xlink:href="#icon-gl-playCircle-copy"></use>
                                </svg>
                            </div>
                            <div class="list">
                                <div class="pic"><img :src="item[2].picUrl" :alt="item[2].name"></div>
                                <div class="name">{{item[2].name}}-{{item[2].song.artists[0].name}}</div>
                                <svg class="icon" aria-hidden="true">
                                    <use xlink:href="#icon-gl-playCircle-copy"></use>
                                </svg>
                            </div>
                            <div class="list">
                                <div class="pic"><img :src="item[3].picUrl" :alt="item[3].name"></div>
                                <div class="name">{{item[3].name}}-{{item[3].song.artists[0].name}}</div>
                                <svg class="icon" aria-hidden="true">
                                    <use xlink:href="#icon-gl-playCircle-copy"></use>
                                </svg>
                            </div>
                        </div>
                        
                    </swiper-slide>
            </swiper>
        </div>
    </div>
</template>

<script>
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';

import {getNewsongs} from '@/api/index.js'

export default {
    data () {
        return {
            songs:[],    
        }
    },
    components: {
      Swiper,
      SwiperSlide,
    },
    setup() {
      const onSwiper = (swiper) => {
        console.log(swiper);
      };
      const onSlideChange = () => {
        console.log();
      };
      return {
        onSwiper,
        onSlideChange,
        // modules: [A11y],
      };
    },
    async mounted () {
        let res = await getNewsongs();
        this.songs = this.chunk(res.data.result, 4);
    },
    methods: {
        chunk:function(arr, size) {
            let rsArr = [];
            for(var i=0;i<arr.length;i+=size){
                var tempArr=[];
                for(var j=0;j<size&&i+j<arr.length;j++){
                tempArr.push(arr[i+j]);
                }
                rsArr.push(tempArr);
            }
            return rsArr;
        }

    }
}

</script>

<style lang="less" scoped>
.newsongs{
    width: 7.5rem;
    .newsongsTop{
        display: flex;
        height: 0.7rem;
        padding: 0 0.3rem;
        align-items: center;
        justify-content: space-between;
        .title{
            font-size: 0.35rem;
            font-weight: 600;
        }
        .more {
            border: 1px solid #ccc;
            font-size: 0.22rem;
            height: 0.4rem;
            width: 6.0em;
            text-align: center;
            line-height: 0.36rem;
            border-radius: 0.5rem;
            .icon{
                margin-top: 0.08rem;
                width: 0.2rem;
                height: 0.2rem;
            }
        }
    }
    .newsongsContent{
        width: 7.5rem;
        .swiper{
            width: 7.5rem;
            margin-top: 0.3rem;
            padding: 0 0.25rem;
            .box{
                display: flex;
                flex-direction: column;
                .list {
                    margin-bottom: 0.1rem;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    .pic{
                    width: 1.0rem;
                        img{
                            width: 1.0rem;
                            height: auto;
                            border-radius: 0.15rem;
                        }
                    }
                    .name{
                        width: 6rem;
                        padding: 0 0.2rem;
                    }
                    .icon{
                        width: 0.6rem;
                        height: 0.6rem;
                    }
                }
            }

        }
    }
}
</style>